import { 
  resetStatus,
  getAllInvoices,
  selectData,
  selectStatus,
  selectItemList,
} from '@/lib/features/document_ocr/document.slice'

import { useAppDispatch, useAppSelector } from "@/lib/hooks/hooks";

export function InvoicesTable({
  currentPageNo,
  offset,
  totalItems
}: {
  currentPageNo: number,
  offset: number;
  totalItems: number;
}) {

  const router = useRouter();

  const dispatch = useAppDispatch();

  const status = useAppSelector(selectStatus);
  const itemList = useAppSelector(selectItemList);

  const initialized = useRef(false)

  let itemPerPage = 10;

  const {
    putValue,
    getValue,
    getAllValue,
    updateValue,
    deleteValue,
    isDBConnecting,
  } = useIndexedDB(DBConfig.name, [DBConfig.invoiceTable]);

  useEffect(() => {
    
    if (!initialized.current) {
      initialized.current = true;
      console.log("EFFECT STATUS >>> ", status);

      if (status === "success") {
        showToast("success", "Data has been saved!");
        dispatch(resetStatus());
      } else if(status === "edit") {
        router.push(`/document_ocr`);
      }

      if(itemList === null)
        dispatch(getAllInvoices());
    }
    
    
    
  }, [status]);

  function prevPage() {
    router.back();
  }

  async function nextPage() {
    
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Invoices</CardTitle>
        <CardDescription>
          Invoice Listing
        </CardDescription>
      </CardHeader>
      <CardContent>
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Item</TableHead>
              <TableHead>Vendor</TableHead>
              <TableHead>Place</TableHead>
              <TableHead>Date</TableHead>
              <TableHead>Type</TableHead>
              <TableHead>Amount</TableHead>
              <TableHead>
                <span>Actions</span>
              </TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {itemList?.length ? (
              itemList?.map((invoice: any) => (
              <InvoiceRow key={invoice.id} invoice={invoice} />
            ))
            ) : (
              <TableRow>
                <TableCell className="font-medium"><span>No record found</span></TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </CardContent>
      <CardFooter>
        
      </CardFooter>
    </Card>
  );
}
